import { Injectable } from '@angular/core';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class BlogService {
  POSTS: Post[]= [];

  constructor(public router: Router) {
    this.username = this.getUsername();
    this.posts = this.POSTS;
    //this.fetchPosts(this.username);
  }

  fetchPosts(username: string): Promise<void> {
    return fetch('http://localhost:3000/api/' + username, {
      method: 'GET',
      credentials: 'include'
    })
    .then(response => response.json())
    .catch(() => {
      //window.location.href = 'http://localhost:3000/login?redirect=/editor/';
    })
    .then(response => {     
      this.posts.length = 0;
      response['body'].forEach(post => {
        let copy = JSON.parse(JSON.stringify(post));
        this.posts.push(copy);
      });
    });
  }

  getPosts(username: string): Post[] { 
    return this.posts;
  }

  getPost(username: string, id: number): Post {
    let result = this.posts.filter(post => post.postid == id);
    if(result.length === 0) {
      return null;
    } else {
      return result[0];
    }
  }

  newPost(username: string): Promise<Post> {
    let newPostId = this.getMaxPostId() + 1;
    let time = Date.now();
    let newPost = new Post();
    newPost.postid    = newPostId;
    newPost.created   = time;
    newPost.modified  = time;
    newPost.title     = '';
    newPost.body      = '';
    this.posts.push(newPost);

    return fetch('http://localhost:3000/api/' + username + '/' + newPostId.toString(), {
      method: 'POST',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({title: "", body: ""})
    })
    .then(response => {
      if(response.status === 201) {
        // success
        return newPost;
      } else {
        for(var i = this.posts.length - 1; i >= 0; i--) {
          if(this.posts[i].postid === newPostId) {
              this.posts.splice(i, 1);
          }
      }
        alert('Status: ' + response.status.toString() + ', Error creating post at server!');
        this.router.navigate(['/']);
        return null;
      }
    });
  }

  updatePost(username: string, post: Post): Promise<void> {
    let result = this.posts.filter(p => p.postid == post.postid);
    if(result.length != 1) {return Promise.resolve();}

    let orig = JSON.parse(JSON.stringify(result[0]));
    
    result[0].modified = Date.now();
    result[0].title = post.title;
    result[0].body = post.body;

    return fetch('http://localhost:3000/api/' + username + '/' + post.postid.toString(), {
      method: 'PUT',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      }, 
      body: JSON.stringify(post)
    })
    .then(response => {
      if(response.status === 200) {
        // Success
      } else {
        alert('Status: ' + response.status.toString() + ', Error updating post at server!');
        result[0].modified = orig.modified;
        result[0].title = orig.title;
        result[0].body = orig.body;
        // DONE: route to edit page
        this.router.navigate(['/' , 'edit', orig.postid]);
      }
    });
  }

  deletePost(username: string, postid: number): Promise<void> {
    let result = this.posts.filter(p => p.postid == postid);
    if(result.length != 1) return;

    let orig = JSON.parse(JSON.stringify(result[0]));
    for(var i = this.posts.length - 1; i >= 0; i--) {
        if(this.posts[i].postid === postid) {
            this.posts.splice(i, 1);
        }
    }

    return fetch('http://localhost:3000/api/' + username + '/' + postid.toString(), {
      method: 'DELETE',
      credentials: 'include',
    })
    .then(response => {
      if(response.status === 204) {
        
      } else {
        this.posts.push(orig);
        alert('Status: ' + response.status.toString() + ', Error deleting post at server!');
        // DONE: ROUTE TO '/' here
        this.router.navigate(['/']);
      }
    });
  }

  getMaxPostId(): number {
    let maxId = 0;
    this.posts.forEach(post => {
      if(post.postid > maxId) maxId = post.postid;
    });
    return maxId;
  }

  getUsername(): string {
    if(!document.cookie) return null;
    let token = parseJWT(document.cookie);
    let username = token.usr;
    if(username) {
      return username;
    } else {
      return null;
    }
  }

  private posts: Post[];
  private username: string;
}

export class Post {
  postid: number;
  created: Date | number;
  modified: Date | number;
  title: string;
  body: string;
}

function parseJWT(token) 
{
    let base64Url = token.split('.')[1];
    let base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    return JSON.parse(atob(base64));
}