import { Component, OnInit } from '@angular/core';
import { Parser, HtmlRenderer } from 'commonmark';
import { Post, BlogService }  from '../blog.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-preview',
  templateUrl: './preview.component.html',
  styleUrls: ['./preview.component.css']
})
export class PreviewComponent implements OnInit {
	post: Post;
	username: string;
	reader: Parser;
	writer: HtmlRenderer;
	titleHtml: string;
	bodyHtml: string;

  constructor(private route: ActivatedRoute, private blogService: BlogService) { }

  ngOnInit() {
  	this.username = this.blogService.getUsername();
  	this.reader = new Parser;
    this.writer = new HtmlRenderer;
    this.route.params.subscribe(params => {
      this.post = this.blogService.getPost(this.username, params.id)
      this.renderPost();
    });

  }

  renderPost(): void {
    this.titleHtml = this.writer.render(this.reader.parse(this.post.title));
    this.bodyHtml = this.writer.render(this.reader.parse(this.post.body));
  }

}
